// src/store/index.js
import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    token: localStorage.getItem('token') || null, // 从 localStorage 加载 token
    chapterIds: [],
    favorites: JSON.parse(localStorage.getItem('favorites')) || [],
    isLoggedIn: !!localStorage.getItem('token'), // 根据 token 设置登录状态
  },
  getters: {

  },
  mutations: {
    addFavorite(state, comicId) {
      if (!state.favorites.includes(comicId)) {
        state.favorites.push(comicId);
        localStorage.setItem('favorites', JSON.stringify(state.favorites));

      }
    },
    removeFavorite(state,comicId){
      const index=state.favorites.indexOf(comicId);
      if(!index>-1){
        state.favorites.splice(index,1);
        localStorage.setItem('favorites',JSON.stringify(state.favorites));
      }
    },
    setToken(state, token) {
      state.token = token;
      state.isLoggedIn = !!token;
      if (token) {
        localStorage.setItem('token', token); // 存储 token
      } else {
        localStorage.removeItem('token'); // 移除 token
      }
    },
    logout(state) {
      state.token = null;
      state.isLoggedIn = false;
    },

  },
  actions: {
    toggleFavorite( { commit ,  state }, comicId){
      if (state.favorites.includes(comicId)) {
        commit('removeFavorite', comicId); // 移除收藏
      } else {
        commit('addFavorite', comicId); // 添加收藏
      }
    },
    login({ commit }, token) {
      commit('setToken', token); // 登录时设置 token
    },
    logout({ commit }) {
      commit('setToken', null); // 退出时清除 token
    },
  },
  modules: {},
});