<template>
  <div id="app" style="font-size: 14px;">
    <nav v-show="isDwon">
      <router-link to="/" v-if="$route.path === '/' || $route.path === '/about' || $route.path === '/booksheif'|| $route.path === '/ranking' || $route.path === '/classify' || $route.path === '/lsfmine'" style="font-size: 12rem; display: flex;flex-wrap: wrap; width: 30rem;">
      <van-icon name="shop-o" size="25" />
      推荐</router-link>
      <router-link to="/about" v-if="$route.path === '/' || $route.path === '/about' || $route.path === '/booksheif'|| $route.path === '/ranking' || $route.path === '/classify' || $route.path === '/lsfmine'" style="font-size: 12rem; display: flex;flex-wrap: wrap; width: 30rem;">
      <van-icon name="setting-o" size="25" />
      更新</router-link>
      <router-link to="/booksheif" v-if="$route.path === '/' || $route.path === '/about' || $route.path === '/booksheif'|| $route.path === '/ranking' || $route.path === '/classify' || $route.path === '/lsfmine'" style="font-size: 12rem; display: flex;flex-wrap: wrap; width: 30rem;">
      <van-icon name="bar-chart-o" size="25"/>
      书架</router-link>
      <router-link to="/lsfmine" v-if="$route.path === '/' || $route.path === '/about' || $route.path === '/booksheif'|| $route.path === '/ranking' || $route.path === '/classify' || $route.path === '/lsfmine'" style="font-size: 12rem; display: flex;flex-wrap: wrap; width: 30rem;">
      <van-icon name="manager-o" size="25" />
      我的</router-link>
      <router-link to="/lsfsearch" v-if="$route.path === '/' || $route.path === '/ranking' || $route.path === '/classify'" class="lsfsearch" :style="{ backgroundColor: navBackground }" style="font-size: 12rem;">
        <button style="background-color: rgba(255, 255, 255,.3); border: 0; width: 25vw; border-radius: 30rem; text-align: left; font-size: 8rem;">
          <img src="./assets/sousuo.png" alt="搜索">
        </button>
      </router-link>
    </nav>
    <router-view />
  </div>
</template>
<script>
export default {
  data() {
    return {
      navBackground: 'transparent', // 初始背景色为透明
      isDwon: true, // 默认显示导航栏
      
    };
  },
  watch: {
    $route() {
      this.isDwon = this.$route.path === '/lsfsearch' && '/lsfmine' &&'/booksheif' && 'about' ? false : true; // 判断当前路由是否为首页，如果是则显示导航栏，否则隐藏
    },
  },
  methods: {
    handleScroll() {
      const scrollY = window.scrollY;
      const opacity = Math.min(scrollY / 200, 1);
      this.navBackground = `rgba(255, 255, 255, ${opacity})`; // 设置背景色为白色，透明度随滚动变化
    },

  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll); // 监听滚动事件
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll); // 清理事件监听
  },
}

</script>

