import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import AboutView from '../views/AboutView.vue'
import BookShelf from '../views/BookShelf.vue'
import LsfMine from '../views/LsfMine.vue'
import LsfSearch from '../views/LsfSearch.vue'
// import { component } from 'vue/types/umd'
import ComicDetail from '../views/ComicDetail.vue'; // 导入新创建的组件

import LsfRecommend from '../views/LsfRecommend.vue'
import LsfRanking from '../views/LsfRanking.vue'
import LsfClassify from '../views/LsfClassify.vue'
import LsfKanmanhua from '../views/LsfKanmanhua.vue'
import LsfDenglu from '../views/LsfDenglu.vue'
import store from '@/store';

import { Lazyload } from 'vant';
import Router from 'vue-router'
Vue.use(Lazyload);
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    children: [
      { path: '/', name: 'recommend', component: LsfRecommend },
      { path: '/ranking', name: 'ranking', component: LsfRanking },
      { path: '/classify', name: 'classify', component: LsfClassify },
    ]
  },
  {
    path: '/about',
    name: 'about',
    component: AboutView
  },
  {
    path: '/booksheif',
    name: 'booksheif',
    component: BookShelf,
    meta: {
      needLogin: true
    }
  },
  {
    path: '/lsfmine',
    name: 'lsfmine',
    component: LsfMine,
    meta: {
      needLogin: true
    }
  },
  {
    path: '/lsfsearch',
    name: 'lsfsearch',
    component: LsfSearch
  },
  {
    path: '/comic', // 动态路由，:id为漫画ID
    name: 'comic-detail',
    component: ComicDetail
  },
  {
    path: '/classify', // 动态路由，:id为漫画ID
    name: 'LsfClassify',
    component: LsfClassify
  },
  {
    path: '/lsfkanmanhua', // 动态路由，:id为漫画ID
    name: 'LsfKanmanhua',
    component: LsfKanmanhua
  },
  {
    path: '/lsfdenglu', // 动态路由，:id为漫画ID
    name: 'LsfDenglu',
    component: LsfDenglu
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  let isLogin = store.state.token;
  if (to.meta.needLogin && !isLogin) {
    console.log('需要登录才能访问的页面');
    next({path: '/lsfdenglu', query: {target: to.fullPath}})
  }else{
    next();
  }
})

const originalReplace = Router.prototype.replace;
const originalPush = Router.prototype.push;
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch((err) => err);
};
Router.prototype.replace = function replace(location, onResolve, onReject) {
  if (onResolve || onReject) return originalReplace.call(this, location, onResolve, onReject);
  return originalReplace.call(this, location).catch((err) => err);
}

export default router
