<template>
  <div class="newview" style="font-size: 16rem;   position: relative;">
    <van-tabs v-model="activeName" class="tabs" @change="onTabChange" style="position: fixed; width: 100vw" >
      <van-tab
        title-class="tab"
        title-style="font-size:11rem; line-height:40rem;"
        line-height="100%"
        line-width="100%"
        style="margin-top: 40rem;"
        v-for="(dayIndex, index) in dayIndexes"
        :key="index"
        :title="days[index]"
      >
      </van-tab>
    </van-tabs>
    <div class="newlist">
      <NewItem v-for="item in lists" :key="item.id" :item="item"></NewItem>
    </div>
  </div>
</template>

<script>
import NewItem from '@/components/NewItem.vue';
import { getDayList } from "@/api/index.js";
export default {
  components:{
      NewItem
  },
  data: function() {
    return {
      activeName: "0", // 默认值
      days: [],
      dayIndexes: [], // 新增数组
      lists: []
    };
  },
  created() {
    this.generateDays();
    const queryActiveName = this.$route.query.activeName;
    if (queryActiveName) {
      this.activeName = queryActiveName; // 设置 activeName
    }
    this.fetchData(this.activeName);
    getDayList(this.activeName).then((res) => {
      this.lists = res.data.data;
    });
    this.fetchData(this.activeName);
  },
  methods: {
    generateDays() {
      const dayIndexesArray = [];
      const daysOfWeek = ['周一', '周二', '周三', '周四', '周五', '周六', '周日'];

      // Generate day indexes from today to 6 days ago
      for (let i = 0; i < 7; i++) {
        dayIndexesArray.push(i.toString());
      }

      this.dayIndexes = dayIndexesArray;

      const today = new Date();
      const todayIndex = today.getDay(); // 0 (Sunday) to 6 (Saturday)
      const daysArray = [];

      // Calculate the index of Monday in the current week
      const mondayIndex = (todayIndex === 0) ? 6 : todayIndex - 1;

      // Generate days from Monday to Sunday
      for (let i = 0; i < 7; i++) {
        const date = new Date(today);
        date.setDate(today.getDate() - (mondayIndex - i + 7) % 7);
        const dayOfWeek = daysOfWeek[i];
        const formattedDay = this.formatDay(date, dayOfWeek);

        daysArray.push(formattedDay);
      }

      this.days = daysArray;
    },
    formatDay(date, dayOfWeek) {
      const today = new Date();
      const yesterday = new Date(today);
      yesterday.setDate(today.getDate() - 1);
      const tomorrow = new Date(today);
      tomorrow.setDate(today.getDate() + 1);

      if (date.toDateString() === today.toDateString()) {
        return '今天';
      } else if (date.toDateString() === yesterday.toDateString()) {
        return '昨天';
      } else if (date.toDateString() === tomorrow.toDateString()) {
        return '明天';
      } else {
        return dayOfWeek;
      }
    },
    onTabChange(name) {
      console.log('当前选中的标签:', name, this.activeName);
    },
    fetchData(activeName) {
      getDayList(activeName).then((res) => {
        console.log(res.data.data);
        
        this.lists = res.data.data;
      })
    }
  },
  watch: {
    activeName(newVal) {
        const currentQuery = this.$route.query;

        // 只有在 activeName 变化时才进行导航
        if (currentQuery.activeName !== newVal) {
            this.$router.replace({ query: { ...currentQuery, activeName: newVal } });
            this.fetchData(newVal);
        }
    }
}
};
</script>
<style lang="less" scoped>
.newview {
  text-align: left;
}
.newlist{
  padding: 50rem 10rem 60rem 10rem;

}
</style>