<template>
  <div class="app" style="font-size: 16rem; text-align: left">
    <!-- 搜索框 -->
    <div class="top">
      <van-icon name="arrow-left" @click="goToTuijian" />
      <input
        type="text"
        placeholder="这个总裁有点残"
        @keyup.enter="search"
        v-model="searchKeyword"
      />
      <button @click="search">搜索</button>
    </div>
    <div class="function" v-if="isShow">
      <!-- 热门搜索 -->
      <div class="hotsearch">
        <h5>热门搜索</h5>
        <span
          style="font-size: 14rem"
          v-for="items in item"
          :key="items.id"
          @click="onSearch(items.title)"
        >
          {{ items.title }}
        </span>
      </div>
      <!-- 搜索历史 -->
      <div class="searchhistory" v-if="query.length > 0">
        <h5>搜索历史</h5>
        <ul>
          <div class="list" v-for="(queryItem, index) in query" :key="index">
            <li @click="onSearch(queryItem)">{{ queryItem }}</li>
            <span @click="deleteHistory(index)">X</span>
          </div>
        </ul>
      </div>
    </div>
    <!-- 搜索结果 -->
    <div class="searchlist" v-else>
      <ul>
        <li
          v-for="result in searchResults"
          :key="result.id"
          @click="gotocomicdetail(result.id)"
        >
          <div class="left">
            <img :src="result.pic" alt="" />
          </div>
          <div class="right">
            <div class="right-top">
              <p>{{ result.title }}</p>
              <span>更新至{{ result.lastchapter }}</span>
            </div>
            <div class="right-buttom">
              <span>{{ result.author }}</span>
              <p>
                <span>{{ result.tag }}</span>
              </p>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { getSoHot, getSoComic } from "@/api/index";

export default {
  data: function () {
    const storedQuery = localStorage.getItem("query");
    return {
      item: [],
      searchKeyword: "",
      searchResults: [],
      query: storedQuery ? JSON.parse(storedQuery) : [],
      isShow: true,
      loading: false,
    };
  },
  created: function () {
    getSoHot().then((res) => {
      this.item = res.data.data.list;
      // console.log(res.data.data.list)
    });
  },
  methods: {
    goToTuijian() {
      this.$router.push("/");
    },
    deleteHistory(index) {
      // 删除指定索引处的历史记录
      this.query.splice(index, 1);
    },
    addToHistory(query) {
      if (!this.query.includes(query)) {
        this.query.push(query);
        // 可选：如果你想要限制历史记录的数量，你可以在这里添加逻辑
        // 例如，只保留最近的10个搜索查询
        if (this.query?.length > 10) {
          this.query.shift(); // 移除最旧的查询
        }
      }
    },
    gotocomicdetail(id) {
      if (id) {
        this.$router.push({ path: "/comic", query: { id } });
      }
    },
    search() {
      if (this.searchKeyword == "") {
        alert("输入框不能为空");
      } else {
        getSoComic({
          keyword: this.searchKeyword,
        })
          .then((res) => {
            this.searchResults = res.data.data.list; // 确保这里正确赋值
            console.log("1111q8y4qgh", this.searchResults);
          })
          .finally(() => {
            console.log("完成");
            this.addToHistory(this.searchKeyword);
            this.searchKeyword = "";
            this.loading = false;
            this.isShow = false;
          });
      }
    },
    // 当用户点击热门搜索或历史记录将内容放回输入框并搜索
    onSearch(query) {
      this.searchKeyword = query;
      this.search();
      this.isShow = false;
    },
  },
  watch: {
    query: function (newL, old) {
      console.log("query发生了变化", newL, old);
      // 把数据存到本地存储
      localStorage.setItem("query", JSON.stringify(newL));
    },
  },
};
</script>

<style lang="less" scoped>
.app {
  .top {
    height: 60rem;
    line-height: 60rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    i {
      width: 20rem;
      height: 20rem;
    }
    input {
      flex: 1;
      height: 30rem;
      padding-left: 20rem;
      margin: 0 20rem;
      border-radius: 20rem;
    }
    button {
      line-height: 40rem;
      width: 50rem;
      height: 40rem;
      border: 0;
      background-color: transparent;
    }
  }
  .hotsearch {
    padding: 0 20rem;
    h5 {
      margin: 10rem 0;
      padding: 0 10rem;
      color: #ccc;
    }
    span {
      display: inline-block;
      height: 30rem;
      border-radius: 20rem;
      border: 1px solid #eaeaea;
      padding: 0 10rem;
      margin-right: 10rem;
      margin-top: 10rem;
      text-align: center;
      line-height: 30rem;
      background-color: #eaeaea;
      cursor: pointer; /* 添加鼠标指针样式 */
    }
  }
  .searchlist {
    ul {
      li {
        margin-bottom: 10rem;
        padding-left: 10rem;
        display: flex;
        .left {
          img {
            width: 100rem;
            height: 150rem;
          }
        }
        .right {
          display: flex;
          flex-direction: column;
          padding-left: 10rem;
          .right-top {
            margin-bottom: 20rem;
          }
          .right-buttom {
            margin-top: 20rem;
          }
          p {
            margin: 5rem 0;
          }
          span {
             overflow: hidden;
            width: 40vw;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            color: #969696;
            font-size: 13rem;
          }
        }
      }
    }
  }
  .searchhistory {
    h5 {
      margin: 10rem 0;
      padding-left: 20rem;
      color: #969696;
    }
    ul {
      .list {
        display: flex;
        justify-content: space-between;
        margin-top: 5rem;

        li {
          padding-left: 20rem;
        }
        span {
          width: 20rem;
          height: 20rem;
          cursor: pointer; /* 添加鼠标指针样式 */
        }
      }
    }
  }
}
</style>