<template>
  <div>
    <van-tabs style="margin-top: 40rem">
      <div class="lsfdaxiao" style="font-size: 16rem">
        <van-tab
          class="custom-font-size"
          v-for="item in list"
          :title="item.name"
          :key="item.id"
          style="padding: 0; margin: 0"
        >
          <LsfRenqibang :id="item.id" :item="item.items" :key="item.id" />
        </van-tab>
      </div>
    </van-tabs>
    <button
      v-if="isVisible"
      @click="scrollToTop"
      style="
        position: fixed;
        bottom: 60rem;
        right: -8rem;
        font-size: 16rem;
        border: 0;
        background: transparent;
      "
    >
      <img src="../assets/fanhui.png" alt="" class="back"/>
    </button>
    <!-- 添加返回顶部按钮 -->
  </div>
</template>

<script>
import LsfRenqibang from "@/components/LsfRenqibang.vue";
import { getTopIndex } from "@/api/index.js";
export default {
  components: {
    LsfRenqibang,
  },
  data() {
    return {
      list: [],
      title: "",
      id: 0,
      isVisible: false,
    };
  },
  created() {
    this.getTopIndexFn();
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.handleScroll);
  },

  methods: {
    handleScroll() {
      // 获取当前滚动位置
      const scrollTop = window.scrollY || document.documentElement.scrollTop;
      // 如果滚动位置超过 200rem，显示按钮
      this.isVisible = scrollTop > 200 ? true : false;
    },
    scrollToTop() {
      // 平滑滚动到顶部
      window.scrollTo({ top: 0, behavior: "smooth" });
    },
    async getTopIndexFn() {
      await getTopIndex().then((res) => {
        console.log(res.data.data);
        this.list = res.data.data;
      });
      this.title = this.$route.query.name;
      this.id = this.$route.query.id;
    },
  },
};
</script>

<style lang="less" scoped>
div {
  .van-tab {
    font-size: 16rem;
    .van-tab--active {
      font-size: 16rem;
    }
  }
}
</style>