<template>
  <div>
    <div class="lsfname" style="margin-bottom: 10rem;">
      {{ item.name }}
    </div>
    <div
      class="lsf_bg"
      v-if="shuffledComics && shuffledComics.length > 0"
      style="box-sizing: border-box;"
    >
          <van-image
          width="100%"
          height= "220rem"
          borderRadius= "5rem"
          lazy-load
          :src="shuffledComics[0]?.bigpic"
          @click="gotocomicdetail(shuffledComics[0].id)"
        />
      <h4 style="margin: 0">{{ shuffledComics[0]?.title }}</h4>
      <p style="margin: 0">{{ shuffledComics[0]?.tag }}</p>
      <!-- </router-link> -->
    </div>
    <ul class="wh">
      <li
        v-for="comic in shuffledComics.slice(1, 10)"
        :key="comic.id"
        @click="gotocomicdetail(comic.id)"
      >
        <van-image
          width="100%"
          height= "160rem"
          borderRadius= "5rem"
          lazy-load
          :src="comic?.pic"
        />
        <div class="lsfwenben">
          <h4>{{ comic?.title }}</h4>
          <p>{{ comic?.tag }}</p>
        </div>
      </li>
      <div class="lsfbtn" style="margin-top: 20rem">
        <button @click="shuffleComics">换一换</button>
        <button @click="loadMore" :disabled="loadingMore">查看更多</button>
      </div>
    </ul>
    <PopupComponent :comics="moreComics" :visible="popupVisible" @update:visible="popupVisible = $event" />
    <div v-if="loadingMore" class="loading">加载中...</div>
  </div>
</template>

<script>
import { getIndexMore } from "../api/index";
import PopupComponent from './PopupComponent.vue';
export default {
  components: {
    PopupComponent
  },
  props: ["item"],
  data() {
    return {
      shuffledComics: null,
      comic: null,
       page: 1, // 用于分页
      moreComics: [], // 存储更多数据
      popupVisible: false, // 控制弹出层的显示
      loadingMore: false // 加载更多时的加载状态
    }
  },
  created() {
    this.shuffledComics = this.item.list; // 初始化为原始列表
    this.getMore();
  },
  methods: {
     getMore() {
      getIndexMore().then((res) => {
        this.comic = res.data.data;
        // console.log('qqqqq',this.comic);
      });
    },
    shuffleComics() {
      this.shuffledComics = this.shuffleArray(this.item.list); // 打乱数组并更新
    },
    shuffleArray(array) {
      // console.log("原数组:", array);
      return array.sort(() => Math.random() - 0.5);
    },
    gotocomicdetail: function (id) {
      if (id) {
        this.$router.push({ path: "/comic", query: { id } });
      }
    },
     loadMore() {
      this.loadingMore = true; // 开始加载更多
      getIndexMore({ page: this.page }).then((res) => {
        this.moreComics = res.data.data;
        this.popupVisible = true; // 数据加载完成后显示弹出层
        this.page += 1; // 更新页码
        this.loadingMore = false; // 结束加载更多
      }).catch((error) => {
        console.error("加载更多数据失败:", error);
        this.loadingMore = false; // 结束加载更多
      });
    }
  },
};
</script>

<style lang="less" scoped>
.wh {
  max-width: 100vw;
  overflow: hidden;
  box-sizing: border-box;

}

div {
  font-size: 16rem;
  text-align: left;

  .lsfname {
    font-size: 18rem;
    margin-top: 20rem;
    padding: 0 10rem;
    font-weight: bolder;
    box-sizing: border-box;
  }

  ul {
    display: flex;
    flex-wrap: wrap;
    padding: 10rem;
    box-sizing: border-box;
    h4 {
      margin: 0;
    }

    p {
      margin: 0;
      margin-bottom: 10rem;
    }

    li:nth-child(3n - 1) {
      margin: 0 8rem;
    }

    li {
      width: 30vw;
      box-sizing: border-box;
      img {
        width: 100%;
        height: 160rem;
      }

      .lsfwenben {
        padding: 10rems 0;

        h4 {
          font-size: 16rem;
          margin: 0;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
        }

        p {
          font-size: 12rem;
          margin: 0;
        }
      }
    }

    .lsfbtn {
      width: 100vw;
      display: flex;
      justify-content: space-evenly;

      button {
        width: 45vw;
        height: 30rem;
        border-radius: 30rem;
        border: 0;
        font-size: 12rem;
      }
    }
  }
  .lsf_bg {
    h4 {
      padding: 0 10rem;
    }
    p {
      padding: 0 10rem;
      font-size: 14rem;
    }
  }
}
</style>