<template>
  <div class="comic_detail" style="font-size: 16rem; position: relative; width: 100vw; height: 100vh"
    v-if="item.bigpic">
 <button @click="goBack" style="
        font-size: 16px;
        position: fixed;
        text-align: left;
        width: 100vw;
        height: 40rem;
        border: 0;
        background: transparent;
        z-index: 1000;
      ">
      <img style="width: 30rem" src="../assets/shangyiji.png" alt="" />
    </button>
    <div class="lsfbg"></div>
    <van-image v-if="item && comic.bigpic" width="100%" height="34vh" lazy-load :src="item?.bigpic" />
    <div class="lsftext">
      <div class="lsfshuju">
        <h5>{{ comic?.title }}</h5>
        <p>{{ comic?.author }}</p>
      </div>
      <div class="lsfteaxt" style="
          display: flex;
          flex-wrap: nowrap;
          flex-direction: column;
          justify-content: space-between;
          align-items: flex-end;
        ">
        <p style="font-size: 12rem; margin: 0">
          {{ comic?.urge_num }}次
          <span style="
              background: white;
              border-radius: 10rem;
              color: black;
              display: inline-block;
              width: 10vw;
              text-align: center;
              font-size: 10rem;
              padding: 2rem 3rem;
            ">催更</span>
        </p>
        <p style="font-size: 12rem; margin: 0">{{ comic?.tag }}</p>
      </div>
    </div>
    <p style="padding: 10rem; margin: 0; font-size: 12rem; line-height: 2">
      {{ comic?.info }}
    </p>

    <div class="lsfrenqi" style="
        display: flex;
        justify-content: space-around;
        text-align: center;
        font-size: 12rem;
      ">
      <div class="lsfleft">
        <i style="font-size: 14rem; color: red"> {{ comic?.num_fav }} </i>
        <p style="margin: 0; margin-top: 5rem">收藏数</p>
      </div>
      <div class="lsfcenter">
        <i style="font-size: 14rem; color: red"> {{ comic?.num_love }} </i>
        <p style="margin: 0; margin-top: 5rem">点赞数</p>
      </div>
      <div class="lsfrught">
        <i style="font-size: 14rem; color: red"> {{ comic?.num_look }} </i>
        <p style="margin: 0; margin-top: 5rem">人气数</p>
      </div>
    </div>
    <div style="
        width: 100vw;
        height: 10rem;
        background: aliceblue;
        margin-top: 20rem;
      "></div>
    <div class="lsfmulu">
      <div style="
          display: flex;
          justify-content: space-between;
          align-items: center;
        ">
        <h2 style="font-size: 16rem; padding: 0 10rem">漫画全集</h2>
        <p style="font-size: 12rem; margin-right: 15rem" @click="showPopup">
          目录>
        </p>
      </div>
      <div class="scroll-container">
        <ul>
          <li v-for="chapter in comic?.chapterlist2" :key="chapter.id" @click="lsflist(chapter.id)">
              <van-image width="30vw" lazy-load v-lazy="chapter?.cover" :src="chapter?.cover" />
              <p style="font-size: 12rem">{{ chapter?.name }}</p>
          </li>
          <van-cell is-link @click="showPopup" style="justify-content: center">
          </van-cell>
          <van-popup v-model="show" closeable round position="bottom" :style="{ height: '80%' }">
            <p style="text-align: center">目录</p>
            <div style="
                margin-bottom: 20rem;
                display: flex;
                justify-content: space-between;
                width: 100vw;
                padding: 0 10rem;
                box-sizing: border-box;
              ">
              <span style="
                  display: inline-block;
                  padding-left: 5rem;
                  font-size: 12rem;
                  border-left: 3rem solid orangered;
                ">全部章节
              </span>
              <button @click="toggleSortOrder" style="border: 0; background: transparent; font-size: 12rem">
        <img style="width: 12rem" src="../assets/paixu.png" alt="" />
        章节{{ isAscending ? '倒序' : '正序' }}
      </button>
            </div>
            <ul style="width: 100vw; height: 100%; display: flex; flex-wrap: wrap">
              <li style="
                  width: 100vw;
                  height: 10vh;
                  margin-bottom: 10rem;
                  display: flex;
                " v-for="item in sortedList" :key="item?.id" @click="lsflist(item.id)">
                <img style="width: 30vw; height: 100%; border-radius: 8rem" :src="item?.cover" alt=""
                  v-lazy="item?.cover" />
                <div style="
                    display: flex;
                    width: 100%;
                    padding: 8rem;
                    justify-content: space-between;
                    align-items: flex-end;
                  ">
                  <div>
                    <p style="font-size: 12rem">{{ item?.name }}</p>
                    <p style="margin-top: 20rem; font-size: 12rem">
                      {{ new Date(item?.updatetime).toLocaleDateString() }}
                    </p>
                  </div>
                  <div style="display: flex; align-items: flex-end">
                    <img style="width: 16rem; margin-right: 5rem" src="../assets/xihuan.png" alt="" />
                    <p style="margin-right: 5rem; font-size: 12rem">
                      {{ item?.num_love }}
                    </p>
                  </div>
                </div>
              </li>
            </ul>
          </van-popup>
        </ul>
      </div>
        <div v-if="showChapters" style="
            height: 80vh;
            width: 100vw;
            position: absolute;
            bottom: 0;
            left: 0;
            background: white;
            border-top: 10rem;
            border-top-right-radius: 10rem;
            border-top-left-radius: 10rem;
          ">
          <ul>
            <li v-for="chapter in list?.chapterlist2" :key="chapter.id">
              <img :src="chapter?.cover" alt="" style="" v-lazy="chapter?.cover" />
              <p style="font-size: 12rem">{{ chapter?.name }}</p>
            </li>
          </ul>
        </div>
    </div>
    <!-- <p>第一个章节：{{ firstChapter?.id }}</p> -->
    <div style="margin-bottom: 20rem; padding: 10rem" class="pinglun">
      <h2>热门评论</h2>
      <ul>
        <li style="display: flex; justify-content: space-around" v-for="comment in comments" :key="comment.id">
          <div class="lsfleft">
            <img style="width: 30rem; border-radius: 50%" :src="comment.uhead" alt="" v-lazy="comment.uhead" />
          </div>
          <div style="width: 80vw" class="lsfcenter">
            <span style="font-size: 12rem">{{ comment.uname }}</span>
            <p style="font-size: 14rem; margin: 0">{{ comment.content }}</p>
            <div class="lsfright" style="text-align: right">
              <img style="width: 12rem" src="../assets/dianzan.png" alt="" /><span>{{ comment.like_count }}</span>
              <img style="width: 12rem; margin-left: 10rem" src="../assets/pinglun.png" alt="" /><span>{{
                comment.reply_count }}</span>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <div class="lsfrecommend" style="margin-left: 10rem; padding-bottom: 40rem">
      <h2>推荐漫画</h2>
      <ul style="display: flex; flex-wrap: wrap">
        <li style="width: 30vw; margin-bottom: 10rem" v-for="recom in recommend" :key="recom.id"
          >
          <van-image width="100%" height="40vw" lazy-load :src="recom?.pic" @click="gotocomicdetail(recom.id)"/>
          <p style="margin: 0; font-size: 12rem; margin-top: 5rem">
            {{ recom?.title }}
          </p>
        </li>
      </ul>
    </div>
    <div class="lsfbtn" style="
        width: 100vw;
        position: fixed;
        bottom: 2.8%;
        left: 0;
        z-index: 100;
        border-radius: 10rem;
      ">
      <button v-if="isFavorite" class="left" style="display: flex; align-items: center" @click="toggleFavorite">
        <img style="width: 30rem; margin-right: 8rem" src="../assets/quxiao.png" alt="收藏" />
        <span>取消收藏</span>
      </button>
      <button v-else @click="toggleFavorite" class="left" style="display: flex; align-items: center">
        <img style="width: 30rem; margin-right: 8rem" src="../assets/shoucang.png" alt="未收藏" />
        <span>收藏漫画</span>
      </button>
      <!-- <button class="right" @click="lsflist(item.id)">续看</button> -->
      <button class="right" @click="continueReading">开始观看</button>
    </div>
  </div>
</template>

<script>
import {
  getComicDetail,
  getBookListChapter,
  getCommentListHot,
  getBookRecommend,
  getBookShowChapter,
} from "@/api/index.js";

export default {
  data() {
    return {
      isAscending: true,//排序状态，true 为正序，false 为倒序
      firstChapter: null,  // 用于存储第一个章节
      comicId: null,
      comic: {},
      item: {},
      list: [],//章节列表
      chapter: [],
      comments: [],
      recommend: [],
      showChapters: false,
      show: false,
      lastReadChapterId: null,
      chapterList: [],
      currentChapterIndex: 0,
    };
  },

  computed: {
        // 返回排序后的章节列表
        sortedList() {
    // 根据 isAscending 判断排序顺序
    return this.isAscending
      ? this.getSortedList(this.list)  // 正序
      : this.getSortedList(this.list, true);  // 倒序
  },

    isFavorite() {
      return this.$store.state.favorites.includes(this.comicId);
    },
  },

  created() {



    this.comicId = this.$route.query.id || localStorage.getItem("lastComicId");
  if (this.comicId) {
    localStorage.setItem("lastComicId", this.comicId);
    this.lastReadChapterId = localStorage.getItem(`lastReadChapterId_${this.comicId}`);
  }

  getComicDetail(this.comicId).then((res) => {
    this.comic = res.data.data;
    this.item = this.comic;
    console.log("item",this.item);
    if (this.comic?.chapterlist2?.length > 0) {
      this.firstChapter = this.comic.chapterlist2[0];
    }
  });

  getBookListChapter(this.comicId).then((res) => {
    this.list = res.data.data || [];
  });

  getCommentListHot(this.comicId).then((res) => {
    this.comments = res.data.data;
  });

  getBookRecommend(this.comicId).then((res) => {
    this.recommend = res.data.data;
  });

  // 加载章节数据
  getBookShowChapter(this.comicId).then((res) => {
    this.chapter = res.data.data;
    console.log("章节数据：", this.chapter);  // 调试，确保章节数据正确加载
  });
  },

  methods: {


    toggleSortOrder() {
      // 切换章节排序顺序
      this.isAscending = !this.isAscending;
    },
    getSortedList(arr, isDescending = false) {
    return [...arr].sort((a, b) => {
      // 使用 updatetime 排序，确保正确比较日期
      const dateA = new Date(a.updatetime);
      const dateB = new Date(b.updatetime);
      
      // 根据 isDescending 确定排序顺序
      if (isDescending) {
        return dateB - dateA;  // 倒序
      } else {
        return dateA - dateB;  // 正序
      }
    });
  },
    lsflist(id) {
      const lastReadChapterId = localStorage.getItem(`lastReadChapterId_${id}`);
      getBookShowChapter(id).then((res) => {
        this.chapter = res.data.data;
        const chapterId = lastReadChapterId || this.chapter[0]?.id;
        this.$router.push({
          path: "/lsfkanmanhua",
          query: { id, chapterId },
        });
      });
    },
    toggleFavorite() {
      this.$store.dispatch("toggleFavorite", this.comicId);
    },
    
    loadChapterList(comicId) {
      getBookListChapter(comicId).then((res) => {
        this.chapterList = res.data.data || [];
      }).catch((error) => {
        console.error("获取章节列表失败:", error);
      });
    },
    
    // 观看第一章漫画
    continueReading() {
    // 确保有章节数据
    if (this.comic?.chapterlist2?.length > 0) {
      if (this.comic?.chapterlist2?.length > 0) {
      // 获取第一章的 ID
      const firstChapterId = this.comic.chapterlist2[0].id;
      const lastReadChapterId = localStorage.getItem(`lastReadChapterId_${this.comicId}`);

      // 使用最后阅读章节 ID 或默认使用第一章 ID
      const chapterId = lastReadChapterId || firstChapterId;

      // 跳转到漫画章节页面，传递漫画 ID 和章节 ID
      this.$router.push({
        path: "/lsfkanmanhua", // 章节页面路径
        query: {  id: chapterId }, // 传递漫画 ID 和章节 ID
      });
    } else {
      console.error("没有章节数据");
    }
  }
  },
    
    
    
    showPopup() {
      this.show = true;
    },
    
    gotocomicdetail(id) {
      if (id) {
        this.$router.replace({ path: "/comic", query: { id } });
        // 让页面重载
      window.location.reload();
      }
    },
    
    viewComic() {
      this.$router.push({ path: "/lsfkanmanhua", query: { id: this.comicId } });
    },
    
    goBack() {
      this.$router.push({ path: "/" });
    },
    
    goToChapter(chapterId) {
      this.$router.push({ name: "Chapter", params: { chapterId } });
      if (this.comicId) {
        localStorage.setItem(`lastReadChapterId_${this.comicId}`, chapterId);
      }
    },
  },
};

</script>

<style lang="less" scoped>
.comic_detail {
  text-align: left;
  position: relative;

  .lsfbg {
    position: absolute;
    width: 100vw;
    height: 34vh;
    background-image: url(../assets/yinying.png);
    z-index: 1;
  }

  img {
    width: 100vw;
  }

  .lsftext {
    box-sizing: border-box;
    padding: 10rem;
    width: 100vw;
    color: white;
    display: flex;
    justify-content: space-between;
    position: absolute;
    top: 23vh;
    left: 0;
    z-index: 11;

    .lsfshuju {

      h5,
      p {
        margin: 0;
      }

      h5 {
        font-size: 16rem;
        margin-bottom: 10rem;
      }

      p {
        font-size: 12rem;
      }
    }
  }

  .scroll-container {
    overflow-y: hidden;
    white-space: nowrap;

    ul {
      display: flex;

      li {
        width: 30vw;
        display: inline-block;
        margin-left: 10px;

        p {
          margin: 0;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
        }
      }
    }
  }

  .lsfmulu {
    .van-cell.van-cell--clickable {
      // display: none;
      margin-top: 10rem;
    }
  }

  .lsfrecommend {
    ul {
      margin-bottom: 50rem;

      li {
        p {
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
        }
      }

      li:nth-child(3n - 1) {
        margin: 0 8rem;
      }
    }
  }

  .lsfbtn {
    background-color: aliceblue;
    display: flex;
    justify-content: space-evenly;
    padding-top: 20rem;

    button {
      width: 40%;
      height: 35rem;
      background-color: aliceblue;
      border: none;
      outline: none;
      border-radius: 30rem;
    }

    .right {
      background-color: rgb(255, 0, 0);
      color: white;
    }
  }

  .left {
    margin-right: 10rem;
  }

  ::-webkit-scrollbar {
    display: none;
  }
}
</style>