<!-- src/views/ComicRead.vue -->
<template>
  <div class="comic-read" style="position: relative">
    <button
      @click="goBack"
      style="
        font-size: 16px;
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 40rem;
        border: 0;
        background: transparent;
        z-index: 1000;
        text-align: left;
      "
    >
      <img style="width: 30rem" src="../assets/shangyiji.png" alt="" />
    </button>
    <p
      style="
        font-size: 16rem;
        margin: 0;
        height: 40rem;
        line-height: 40rem;
        position: fixed;
        width: 100vw;
        top: 0;
        left: 0;
        background: white;
      "
    >
      {{ comic?.name }}
    </p>

    <ul style="width: 100vw; margin-top: 40rem; padding-bottom: 40rem">
      <li
        style="width: 100vw; display: flex"
        v-for="item in comic.piclist"
        :key="item?.id"
      >
        <img style="width: 100%" :src="item?.url" alt="" />
      </li>
    </ul>
    <ol>
      <li>
        <button @click="loadPrevComic">上一章</button>
        <button @click="loadNextComic">下一章</button>
      </li>
    </ol>
  </div>
</template>

<script>
import { getBookShowChapter } from "@/api/index.js"; // 假设您有一个API来获取漫画内容

export default {
  data() {
    return {
      comic: [],
      previousComicId: null, // 存储上一个界面的漫画 ID
      book:null
    };
  },
  created() {
    const comicId = this.$route.query.id; // 从路由参数中获取漫画ID
    // this.previousComicId = this.$route.query.previousId || null; // 存储上一个界面的漫画 ID
    // console.log( "1111",this.previousComicId ,);
    
    getBookShowChapter(comicId).then((res) => {
      console.log("11", res.data.data);
      this.comic = res.data.data;
      this.book = res.data.data.comicid
       // 将获取到的漫画数据赋值给comic
      // console.log("111", res, this.book);
    });
  },
  methods: {
    goBack() {
        this.$router.push({path: "/comic", query: { id: this.book }}); // 如果没有上一个界面的漫画 ID，则返回上一页
    },
    // 其他方法
    // 其他方法
    loadPrevComic() {
      let currentComicId = parseInt(this.$route.query.id, 10);
      if (!isNaN(currentComicId) && currentComicId > 1) {
        currentComicId -= 1;
        getBookShowChapter(currentComicId).then((res) => {
          if (res.data.data) {
            this.comic = res.data.data; // 替换现有的漫画数据
          } else {
            console.warn("No previous comic available.");
          }
        });
        this.$router
          .push({
            path: "/lsfkanmanhua",
            query: { id: currentComicId },
          })
          .then(() => {
            this.scrollToTop(); // 滚动到顶部
          });
      } else {
        console.warn("Already at the first comic.");
      }
    },
    loadNextComic() {
      let currentComicId = parseInt(this.$route.query.id, 10);
      if (!isNaN(currentComicId)) {
        currentComicId += 1;
        getBookShowChapter(currentComicId).then((res) => {
          if (res.data.data) {
            this.comic = res.data.data; // 替换现有的漫画数据
          } else {
            console.warn("No more comics available.");
          }
        });
        this.$router
          .push({
            path: "/lsfkanmanhua",
            query: { id: currentComicId },
          })
          .then(() => {
            this.scrollToTop(); // 滚动到顶部
          });
      } else {
        console.error("Invalid comicId:", this.$route.query.id);
      }
    },
    scrollToTop() {
      window.scrollTo(0, 0); // 滚动到顶部
    },
  },
};
</script>

<style scoped>
.comic-read {
  text-align: center;
}
nav {
  padding: 0;
}
ol {
  li {
    padding-top: 10rem;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    position: fixed;
    left: 0;
    bottom: 3%;
    background-color: aliceblue;
    button {
      width: 30vw;
      height: 30rem;
      border: 0;
      background: rgb(255, 98, 0);
      font-size: 16px;
      color: white;
      border-radius: 30rem;
    }
    button:hover {
    }
  }
}
</style>