<template>
  <div class="cate">
    <van-pull-refresh
      v-model="isLoading"
      @refresh="onRefresh"
      style="font-size: 16rem; text-align: left; padding: 10rem"
    >
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        :immediate-check="false"
        style="margin-bottom: 30rem"
      >
        <ul class="filter" v-if="filter" style="margin-top: 50rem">
          <li>
            <button
              @click="myfilter = { ...myfilter, cateid: item.id }"
              :class="{ active: myfilter.cateid == item.id }"
              v-for="item in [{ id: 0, title: '全部' }, ...filter.category]"
              :key="item.id"
            >
              {{ item.title }}
            </button>
          </li>
          <li>
            <button
              @click="myfilter = { ...myfilter, end: item.id }"
              :class="{ active: myfilter.end == item.id }"
              v-for="item in filter.end"
              :key="item.id"
            >
              {{ item.title }}
            </button>
          </li>
          <li>
            <button
              @click="myfilter = { ...myfilter, free: item.id }"
              :class="{ active: myfilter.free == item.id }"
              v-for="item in filter.free"
              :key="item.id"
            >
              {{ item.title }}
            </button>
          </li>
          <li>
            <button
              @click="myfilter = { ...myfilter, sort: item.id }"
              :class="{ active: myfilter.sort == item.id }"
              v-for="item in filter.sort"
              :key="item.id"
            >
              {{ item.title }}
            </button>
          </li>
        </ul>

        <ul class="books">
          <li
            v-for="item in books"
            :key="item.id"
            @click="gotocomicdetail(item.id)"
          >
            <van-image
              style="width: 30vw; height: 40vw;"
              :src="item.pic"
              lazy-load
              fit="cover"
              class="rounded-image"
            />
            <h5>{{ item.title }}</h5>
            <span> 更新至 : {{ item.lastchapter }}</span>
          </li>
        </ul>
      </van-list>
    </van-pull-refresh>
    <button
      v-if="isVisible"
      @click="scrollToTop"
      style="
        position: fixed;
        bottom: 60rem;
        right: -8rem;
        font-size: 16rem;
        border: 0;
        background: transparent;
      "
    >
      <img src="../assets/fanhui.png" alt="" class="back"/>
    </button>
    <!-- 添加返回顶部按钮 -->
  </div>
</template>

<script>
import { Toast } from "vant";
import { getHaokanCategory, getHaokanCategoryBooks } from "@/api/index.js";
// getHaokanCategoryBooks;

export default {
  data() {
    let {
      p = 1,
      cateid = 0,
      end = "0",
      free = "0",
      sort = "0",
    } = this.$route.query;

    return {
      filter: null,

      myfilter: { cateid, end, free, sort },
      p: Number(p),

      books: [],

      loading: false,
      finished: false,
      myloading: false,

      isLoading: false,
      isVisible: false,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  created: function () {
    getHaokanCategory().then((res) => {
      this.filter = res.data.data;
    });
    this.getHaokanCategoryBooksApiReplace(this.myfilter);
  },

  computed: {
    query: function () {
      return { ...this.myfilter, p: this.p };
    },
  },

  methods: {
    getHaokanCategoryBooksApiReplace(options) {
      this.p = 1;
      this.finished = false;
      return getHaokanCategoryBooks(options).then((res) => {
        // this.books = [...this.books, ...res.data.data];
        this.books = res.data.data;
      });
    },
    getHaokanCategoryBooksApiPush(options) {
      return getHaokanCategoryBooks(options).then((res) => {
        this.books = [...this.books, ...res.data.data];

        // this.books = res.data.data;
        this.loading = false;
        this.myloading = false;
        if (res.data.data.length == 0) {
          this.finished = true;
        }
      });
    },
    gotocomicdetail: function (id) {
      if (id) {
        this.$router.replace({ path: "/comic", query: { id } });
      }
    },

    onLoad() {
      console.log("触底了", this.finished, this.myloading);

      if (this.p > 4) {
        this.finished = true;
      } else {
        if (!this.myloading && !this.finished) {
          console.log("触底了xxxx", this.finished, this.myloading);
          this.myloading = true;
          console.log("yyy", this.finished, this.myloading);
          this.p++;
          this.getHaokanCategoryBooksApiPush({
            ...this.myfilter,
            p: this.p,
          });
        }
      }
    },

    onRefresh() {
      this.isLoading = false;
      this.getHaokanCategoryBooksApiReplace(this.myfilter).then(() => {
        Toast("刷新成功");
      });
    },
    handleScroll() {
      // 获取当前滚动位置
      const scrollTop = window.scrollY || document.documentElement.scrollTop;
      // 如果滚动位置超过 200rem，显示按钮
      this.isVisible = scrollTop > 200 ? true : false;
    },
    scrollToTop() {
      // 平滑滚动到顶部
      window.scrollTo({ top: 0, behavior: "smooth" });
    },
  },

  watch: {
    myfilter: function (f) {
      this.getHaokanCategoryBooksApiReplace(f);
    },
    query: function (query, old) {
      console.log(query, old);

      if (
        new URLSearchParams(query).toString() !==
        new URLSearchParams(old).toString()
      ) {
        this.$router.replace({
          path: "/classify",
          query,
        });
      }
    },
  },
};
</script>

<style lang="less" scoped>
.filter {
  li {
    margin-bottom: 10px;

    button {
      font-size: 12rem;
      padding: 5px 10px;
      border: 0;
      background: transparent;

      &.active {
        color: white;
        background: red;
        border-radius: 5rem;
      }
    }
  }
}

.books {
  display: flex;
  flex-wrap: wrap;

  li:nth-child(3n - 1) {
    margin: 0 8rem;
  }
  .rounded-image{
    border-radius: 2rem;
    overflow: hidden
  }
  li {
    width: 30vw;

    img {
      border-radius: 20rem;
    }

    h5 {
      margin: 0;
      font-size: 14rem;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
    }

    span {
      font-size: 12rem;
      color: gray;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      padding: 5rem 0;
    }
  }
}
</style>