<template>
    <div id="app" style="font-size: 16rem; position: relative; width: 100vw; height: 100vh;">
        <div style="width: 100vw; height: 15vw; text-align: left; border-bottom: 1rem solid #ccc;">
            <button @click="goBack"
                style="font-size: 16px; width: 50rem; height: 56rem;border: 0; background: transparent;">
                <img style="width: 30rem;" src="../assets/shangyiji.png" alt="">
            </button>
        </div>
        <h1 style="font-size: 18rem; margin: 20rem 0; ">登录</h1>
        <div class="lsfinp">
            <input class="username" type="text" placeholder="手机号码 (+86)" v-model="username">
            <input class="password" type="text" placeholder="输入密码" v-model="password">
        </div>
        <div class="btn" style="display: flex;justify-content: space-around;">
            <button @click="lsfdenglu">登录</button>
            <button @click="lsfzhuce">注册</button>
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex'
import {Toast} from "vant"

export default {
    data() {
        return {
            username: '',
            password: '',
            users: JSON.parse(localStorage.getItem('users')) || []
        }
    },

    methods: {
        ...mapMutations(['setToken']),

lsfdenglu() {
    let user = this.users.find(item => item.username == this.username);
    // console.log("user",user);
    
    
    setTimeout(() => {
        if (user) {
            if (user.password == this.password) {
                // 登录成功
                Toast.success('登录成功');
                this.setToken(user.username);
                this.$router.replace(this.$route.query.target);
            } else {
                // 密码错误
                Toast.fail('密码错误');
            }
        } else {
            // 用户不存在
            Toast.fail('用户不存在');
        }
    }, 1000);
},
lsfzhuce() {
    // 判断用户名和密码是否为空（可根据需求修改）
    if (!this.username || !this.password) {
        this.$toast.fail('用户名或密码不能为空');
        return;
    }

    // 检查用户名是否已经存在
    let existingUser = this.users.find(item => item.username === this.username);
    if (existingUser) {
        this.$toast.fail('用户已存在');
        return;
    }

    // 用户注册成功，添加到 users 数组
    this.users = [
        ...this.users, {
            username: this.username,
            password: this.password
        }
    ];

    // 提示注册成功
    this.$toast.success('注册成功');
},
goBack() {
    this.$router.go(-1); // 返回到上一个页面
}
    },
    watch: {
        users: function (users) {
            localStorage.setItem('users', JSON.stringify(users))
        }
    }
}
</script>

<style lang="less" scoped>
    #app {
       
        .lsfinp {
            .username {
                margin-bottom: 10rem;
                width: 90vw;
                height: 50rem;
                border-radius: 30rem;
                padding: 0 20rem;
                box-sizing: border-box;
            }
            .password {
                margin-top: 10rem;
                width: 90vw;
                height: 50rem;
                border-radius: 30rem;
                padding: 0 20rem;
                box-sizing: border-box;
            }
        }
        .btn {
            button {
            width: 40vw;
            height: 40rem;
            margin-top: 20rem;
            border-radius: 30rem;
            background: #ff4e00;
            border: 0;
            color: white;
        }
        }
     }
</style>