<template>
  <div>
    <ul>
      <li
        v-for="item in liebiao"
        :key="item.id"
        @click="gotocomicdetail(item.id)"
      >
        <div class="lsfbig">
          <van-image
            width="20vw"
            height="25vw"
            borderRadius="5rem"
            lazy-load
            :src="item?.pic"
          />
          <div class="lsfnibi">
            <div class="lsfsmall">
              <h4>{{ item?.title }}</h4>
              <h5>更新至{{ item?.lastchapter }}</h5>
              <h6>{{ item?.author }}</h6>
              <span style="font-size: 10rem"> {{ item?.tag }}</span>
            </div>
            <!-- <span style="font-size: 32rem; font-weight: 600">{{
              item?.topnumber
            }}</span> -->
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { getTopList } from "@/api/index.js";
export default {
  data() {
    return {
      liebiao: [],
    };
  },
  props: {
    id: {
      type: String,
      default: "1",
    },
  },

  created() {
    // 如果 items 被传递，使用它来初始化 liebiao
    if (this.items && this.items.length > 0) {
      this.liebiao = this.items;
    } else {
      console.log(this.id);
      getTopList({ id: this.id }).then((res) => {
        console.log(res.data.data);
        this.liebiao = res.data.data;
      });
    }
  },
  methods: {
    gotocomicdetail: function (id) {
      if (id) {
        this.$router.push({ path: "/comic", query: { id } });
      }
    },
  },
};
</script>

<style lang="less" scoped>
div {
  font-size: 16rem;
  position: relative;

  ul {
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 70rem;
    li {
      width: 100vw;
      margin: 5rem 10rem 5rem 10rem;

      img {
        width: 20vw;
        height: 25vw;
      }

      .lsfbig {
        display: flex;
        text-align: left;

        .lsfnibi {
          width: 70vw;
          display: flex;
          justify-content: space-between;
          align-items: center;

          .lsfsmall {
            margin-left: 8rem;

            h4,
            h5,
            h6 {
              width: 35vw;
              font-size: 14px;
              color: #666;
              margin: 0;
              overflow: hidden;
              text-overflow: ellipsis;
              display: -webkit-box;
              -webkit-line-clamp: 1;
              -webkit-box-orient: vertical;
            }

            h5 {
              font-size: 12rem;
              margin: 4rem 0;
              color: #999;
              font-weight: 200;
            }

            h6 {
              font-size: 12rem;
              color: #999;
              font-weight: 200;
              margin-top: 15rem;
            }

            span {
              display: inline-block;
              color: #999;
              margin-top: 10rem;
            }
          }
        }
      }
    }
  }
}
</style>