<template>
  <div class="home">
    <div class="lsf">
      <van-swipe
        class="my-swipe"
        :autoplay="3000"
        indicator-color="red"
        v-if="banners.length"
      >
        <van-swipe-item v-for="(item, index) in banners" :key="index">
          <van-image
            fit="cover"
            width="100vw"
            height="75vw"
            :src="item.pic"
            v-lazy="item.pic"
          />
        </van-swipe-item>
      </van-swipe>
      <template v-for="item in tabs">
        <HomeView1 v-if="item.pictype === 'h'" :key="item.id" :item="item" />
        <HomeView2 v-if="item.pictype === 'wh'" :key="item.id" :item="item" />
        <HomeView3 v-if="item.pictype === 'ww'" :key="item.id" :item="item" />
      </template>
    </div>
    <button
      v-if="isVisible"
      @click="scrollToTop"
      style="
        position: fixed;
        bottom: 60rem;
        right: -8rem;
        font-size: 16rem;
        border: 0;
        background: transparent;
      "
    >
      <img src="../assets/fanhui.png" alt="" class="back"/>
    </button>
    <!-- 添加返回顶部按钮 -->
  </div>
</template>

<script>
import { getIndexIndex } from "@/api/index.js";
import HomeView1 from "@/components/HomeView1.vue";
import HomeView2 from "@/components/HomeView2.vue";
import HomeView3 from "@/components/HomeView3.vue";

export default {
  name: "HomeView",
  components: {
    HomeView1,
    HomeView2,
    HomeView3,
  },
  data() {
    return {
      id: 13,
      banners: [],
      tabs: [],
      currentItem: {}, // 当前项的数据
      isVisible: false,    
    };
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  created: function () {
    getIndexIndex().then((res) => {
      // console.log(res.data);
      this.banners = res.data.data.recommend;
      this.tabs = res.data.data.tab;
    });
  },
  
  methods: {
    handleScroll() {
      // 获取当前滚动位置
      const scrollTop = window.scrollY || document.documentElement.scrollTop;
      // 如果滚动位置超过 200rem，显示按钮
      this.isVisible = scrollTop > 400 ? true : false;
    },
    scrollToTop() {
      // 平滑滚动到顶部
      window.scrollTo({ top: 0, behavior: "smooth" });
    },
  },
};
</script>

<style>
.lsf {
  position: relative;
  width: 100vw;
  height: 100vh;
} 
.my-swipe .van-swipe-item {
  text-align: center;
}
.back{
  width: 35px;
  height: 35px;
  margin-right: 10px;
  margin-bottom: 10px;
}
</style>