<script>
import { mapActions } from "vuex";
export default {

    data() {
        return {
            show: false,
            currentUser: null,
            users: JSON.parse(localStorage.getItem('users')) || []
        };
    },
created(){
    this.currentUser = this.users.find(user => user.username === this.$store.state.token); // 如果用 Vuex 存储 token
    console.log(this.currentUser);
    
},
    methods: {
        ...mapActions(["logout"]),
        showPopup() {
            this.show = true;
        },
        async logout() {
      await this.$store.dispatch('logout'); // 调用 Vuex 的 logout action
      this.$router.push('/'); // 导航到推荐页
    }
    },
};
</script>

