<template>
    <li @click="gotocomicdetail(comic.id)" v-if="comic" >
        <img :src="comic?.pic" alt="" style="width: 100%; height: 160rem;
         border-radius: 10rem;" v-lazy="comic?.pic" />
        <div class="lsfwenben">
            <h4>{{ comic?.title }}</h4>
            <p>{{ comic?.tag }}</p>
            <button v-if="isEditing" @click="deleteBook"></button>
        </div>
    </li>
</template>
<script>
import { getComicDetail } from '@/api/index.js'; // 确保路径正确
export default {
    props: ['bookid','isEditing'],
    data() {
        return {
            comic: null
        }
    },
    created() {
        getComicDetail(this.bookid ).then((res) => {
            // this.$store.commit('setBookshelf', res.data); // 存储书架数据
            this.comic = res.data.data; // 存储书架数据
            console.log(this.comic);
        });
    },
    methods: {
        gotocomicdetail: function (id) {
            if (id) {
                this.$router.replace({ path: "/comic", query: { id } });
            }
        },
         deleteBook() {
            this.$emit('delete-book', this.bookid);
        },
    }
}
</script>

<style lang="less" scoped>
li {
    width: 30vw;
    display: flex;
    align-items: center;
    margin-bottom: 5rem;
    flex-wrap: wrap;
    position: relative;
    img {
        width: 100px;
    }
    .lsfwenben {
        margin-left: 10px;
        h4 {
            font-size: 14px;
            overflow: hidden;  
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            margin: 0;
            margin-top: 8rem;
        }
        p {
            font-size: 12px;
          
            margin: 0;
              margin-top: 3rem;
        }
        button {
            position: absolute;
            right: 3%;
            top: 3%;
            background: #fff;
            border: 0;
            width: 15rem;
            height: 15rem;
            text-align: center;
            cursor: pointer;
        }
    }
}
li:nth-child(3n - 1){
    margin: 0 8px;
}
</style>