<template>
  <div v-if="visible" class="popup">
    <div class="popup-content">
      <div class="btn">
        <button @click="closePopup">
          <img src="../assets/shangyiji.png" alt="" />
        </button>
      </div>
      <ul>
        <li
          v-for="(comic, index) in comics"
          :key="index"
          @click="gotocomicdetail(comic.id)"
        >
          <van-image
          width="30vw" 
          height= "40vw" 
          borderRadius= "5rem"
          lazy-load
          :src="comic?.pic"
        />
          <div class="lsfwenben">
            <h4>{{ comic?.title }}</h4>
            <p>{{ comic?.author }}</p>
            <p>{{ comic?.tag }}</p>
            <p> 人气: {{ String(comic?.num_look).slice(0,4)}}万</p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "PopupComponent",
  props: ["comics", "visible"],
  methods: {
    closePopup() {
      this.$emit("update:visible", false);
    },
    gotocomicdetail(id) {
      if (id) {
        this.$router.push({ path: "/comic", query: { id } });
      }
    },
  },
  mounted() {
    const ids = this.comics.map((comic) => comic.id);
    const uniqueIds = new Set(ids);
    if (ids.length !== uniqueIds.size) {
      console.error(
        "Duplicate IDs detected:",
        ids.filter((id) => ids.indexOf(id) !== ids.lastIndexOf(id))
      );
    } else {
      console.log("All IDs are unique:", ids);
    }
  },
};
</script>

<style lang="less" scoped>
.popup {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1;
}

.popup-content {
  background-color: white;
  // padding: 20px;
  border-radius: 10px 10px 0 0;
  max-height: 80vh;
  overflow-y: auto;
  position: relative;
  .btn {
    position: fixed;
    width: 100vw;
    height: 10vw;
    background-color: white;
    z-index: 100;
    button {
      border: 0;
      background-color: transparent;
    }
  }
  ul {
    margin-top: 10vw;
    padding-bottom: 30rem;
    li {
      margin-bottom: 10rem;
      display: flex;
      .lsfwenben {
        margin-left: 5vw;
        p {
          margin: 0;
          margin-bottom: 8rem;
        }
      }
    }
  }
}
</style>