<!-- src/views/BookShelf.vue -->
<template>
  <div class="box"  v-if="booklist.length === 0">
      <img src="../assets/tips.jpg" alt="" class="tip">
      <button class="btn" @click="gotoclassify()">戳我逛一逛</button>
    </div>
  <div class="lsf" style="font-size: 16rem; margin-top: 20rem"  v-else>
    <div class="lsfbianji" style="margin-bottom: 10rem">
      <h1 style="margin-bottom: 10rem">书架</h1>
      <button @click="toggleEdit">{{ isEditing ? "取消" : "编辑" }}</button>
    </div>
    <!-- <p v-if="booklist.length === 0" style="text-align: center; font-size: 18rem; color: #888;">书架为空，快去添加书籍吧~</p> -->
    
    <ul>
      <LsfBookitem
        v-show="booklist"
        v-for="bookid in booklist"
        :bookid="bookid"
        :key="bookid"
        :isEditing="isEditing"
        :isSelected="selectedBooks.includes(bookid)"
        @delete-book="removeBook"
        @select-book="toggleSelect"
      >
      </LsfBookitem>
    </ul>
  </div>
</template>

<script>
// import { mapGetters } from 'vuex';
import LsfBookitem from "../components/LsfBookitem.vue"; // 确保路径和文件名正确

export default {
  components: {
    LsfBookitem,
  },
  data() {
    return {
      booklist: JSON.parse(localStorage.getItem("favorites")) || [],
      isEditing: false,
       selectedBooks: []
    };
  },

  computed: {
    // ...mapGetters(['getBookshelf']), // 获取书架数据
    bookshelf() {
      return this.getBookshelf; // 返回书架数据
      // console.log(this.bookshelf);
    },
  },

  methods: {
    gotoclassify(){
      this.$router.push({ name: 'classify' }); // 使用路由名称跳转
    },
    toggleEdit() {
      this.isEditing = !this.isEditing;
        if (!this.isEditing) {
                this.selectedBooks = [];
            }
    },
 selectAll() {
            this.selectedBooks = [...this.booklist];
        },
        toggleSelect(bookid) {
            if (this.selectedBooks.includes(bookid)) {
                this.selectedBooks = this.selectedBooks.filter(id => id !== bookid);
            } else {
                this.selectedBooks.push(bookid);
            }
        },
        removeBook(bookid) {
            this.booklist = this.booklist.filter(id => id !== bookid);
            localStorage.setItem('favorites', JSON.stringify(this.booklist));
        },
        removeSelectedBooks() {
            this.booklist = this.booklist.filter(id => !this.selectedBooks.includes(id));
            localStorage.setItem('favorites', JSON.stringify(this.booklist));
            this.selectedBooks = [];
        },
        gotocomicdetail: function (id) {
            if (id) {
                this.$router.push({ path: "/comic", query: { id } });
            }
        }
  },
};
</script>

<style lang="less" scoped>
.box{
  width: 375px;
  // height: 70px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  // background-color: pink;
  position: fixed;
  left: 0;
  top: 150px;
  .tip{
    width: 100%;
  }
  .btn{
    color: white;
    width: 110px;
    height: 30px;
    margin-top: 20px;
    background-color: #ef4949;
    border: none;
    border-radius: 25px;
  }
}
.lsf {
  box-sizing: border-box;
  .lsfbianji {
    display: flex;
    justify-content: center;
    position: relative;
    h1 {
      margin: 0;
      font-size: 18rem;
      box-sizing: border-box;
    }
    button {
      position: absolute;
      right: 5%;
      border: 0;
      background-color: transparent;
    }
  }

  ul {
    display: flex;
    flex-wrap: wrap;
    padding: 10rem;
  }
}
</style>