import axios from "./axios.js";

const getIndexIndex = () => axios.get("/index/index"); //获取首页数据

const getndexExchangeI = () => axios.get("/index/exchange"); //获取首页数据

const getIndexMore = () => axios.get("/index/more"); //获取首页数据

const getTopIndex = (id) => axios.get(`/top/index?id=${id}`); //获取排行榜

const getTopList = (data = {}) => axios.get('/top/list', { params: data }); //获取排行榜列表

const getHaokanCategory = () => axios.get("/category/list"); //获取分类
const getHaokanCategoryBooks = (options) =>
    axios.get("/book/list", {
        params: options,
    }); 
    
const getSoHot = () => axios.get("/so/hot"); //获取最热

// https://apis.netstart.cn/haokan/so/comic?keyword=总裁&p=1
const getSoComic = (options) => axios.get("/so/comic", {
    params: options,
});         

const getComicDetail = (id) => axios.get(`/book/show?id=${id}`); //获取漫画详情

const getExchangeData = (id) => axios.get(`/index/exchange?id=${id}`); // 添加获取新数据的函数

const getBookShowChapter = (id) => axios.get(`/book/showChapter?id=${id}`);    //获取章节内容

const getBookListChapter = (id) => axios.get(`/book/listChapter?id=${id}`); //获取章节列表

const getDayList = (id) => axios.get(`/day/list?id=${id}`);  //获取每日推荐

const getCommentListHot= (did) => axios.get(`/comment/listHot?did=${did}`); //获取评论列表

const getBookRecommend = (comicId) => axios.get(`/book/recommend?id=${comicId}`); //获取推荐列表

export {
    getBookListChapter, getBookShowChapter, getIndexIndex, getndexExchangeI, getTopIndex,
    getTopList, getHaokanCategory, getHaokanCategoryBooks, getSoHot, getSoComic, getComicDetail,
    getExchangeData ,getDayList, getCommentListHot ,getBookRecommend, getIndexMore
};
