<template>
  <div id="app">
    <nav :style="{ backgroundColor: navBackground }">
      <router-link to="/">推荐</router-link>
      <router-link to="/ranking">榜单</router-link>
      <router-link to="/classify">分类</router-link>
    </nav>
    <router-view />
  </div>
</template>
<script>
export default {
  data() {
    return {
      navBackground: 'transparent', // 初始背景色为透明
    };
  },
  methods: {
    handleScroll() {
      const scrollY = window.scrollY;
      const opacity = Math.min(scrollY / 200, 1);
      this.navBackground = `rgba(255, 255, 255, ${opacity})`; // 设置背景色为白色，透明度随滚动变化
    },
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll); // 监听滚动事件
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll); // 清理事件监听
  },
};
</script>

<style lang="less" scoped>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  position: relative;

  nav {
    background-color: aliceblue; // 设置导航背景色
    transition: opacity .3s; // 添加过渡效果

    width: 66vw;
    position: fixed;
    background: transparent;
    top: 0;
    left: 0;
    height: 20rem;
    padding: 15rem 0;

    a {
      font-weight: bold;
      color: #2c3e50;

      &.router-link-exact-active {
        color: #cf2828;
      }
    }
  }

}
</style>